import React from "react";
import { Modal, ModalHeader, ModalBody, Button, ModalFooter } from "reactstrap";
import AudioPlayer from "../AudioPlayer/AudioPlayer";
const AudioPlayerModal = props => {
  return (
    <div>
      <Modal
        isOpen={props.openFlag}
        toggle={props.toggleFunction}
        className={"modal-md "}
      >
        <ModalHeader>{props.audio.FileName}</ModalHeader>
        <ModalBody>
          <AudioPlayer
            title={props.audio.FileName}
            source={props.audio.FilePath}
            muted={props.muted}
            autoPlay={props.autoPlay}
          />
        </ModalBody>
        <ModalFooter>
          <Button color="danger" onClick={props.toggleFunction}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default AudioPlayerModal;



