import React, { Component } from "react";
import { Col, FormGroup, Row } from "reactstrap";
import CardLayout from "../../components/Cards/CardLayout";
import Loader from "../../components/Loader/Loader";
import AudioCards from "../../components/Cards/AudioCards";
import * as actions from "../../store/actions";
import { connect } from "react-redux";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import ActiveAudioGrid from "./ActiveAudioGrid";
import InactiveAudioGrid from "./InactiveAudioGrid";
import AudioPlayer from "../../components/AudioPlayer/AudioPlayer";
import ConfirmModal from "../../components/Modal/ConfirmModal";
import _ from "lodash";
import { Link } from "react-router-dom";
import AudioPlayerModal from "../../components/Modal/AudioPlayerModal";
import DropdownSelect from "../../components/InputElement/Dropdown";
import * as constants from "../../constants/StatusConstants";

class AudioContent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      renderURL: "",
      showGridView: false,
      audioToDelete: "",
      modalStatus: false,
      showAudioView: false,
      currentAudio: "",
      tableStatus: true
    };
  }
  componentDidMount() {
    this.props.getAudioFiles();
    let compRef = this;
    setTimeout(() => {
      compRef.setState({ loading: false });
      if (compRef.props.audioError) {
        Toaster.Toaster("Something went wrong !", compRef.props.audioError);
      }
    }, 2000);
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  onDeleteAudio(cell, row) {
    if (this.state.tableStatus) {
      return (
        <Link to={this} onClick={() => this.confirmDelete(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    } else {
      return (
        <Link to={this} onClick={() => this.confirmDelete(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
  }
  confirmDelete(audio) {
    this.setState({
      audioToDelete: audio
    });
    this.onModalToggle();
  }
  onModalToggle() {
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
    let audio = _.pick(this.state.audioToDelete, ["Id", "Active"]);
    let displayMessage = "";
    if (this.state.tableStatus) {
      audio.Active = false;
      displayMessage = "Audio deactivated successfully";
    } else {
      audio.Active = true;
      displayMessage = "Audio activated successfully";
    }
    audio.ActiveBy = localStorage.getItem("user");
    audio.ActiveOn = new Date();
    this.props.deleteAudioFile(audio.Id, audio);
    this.setState({ loading: true });
    setTimeout(() => {
      let message = "";
      this.props.audioError
        ? (message = this.props.audioError)
        : (message = displayMessage);
      this.setState({ loading: false });
      Toaster.Toaster(message, this.props.audioError);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  playAudio(cell, row) {
    return (
      <Link to={this} onClick={() => this.onPlayAudio(row)}>
        <i className="fa fa-play-circle play-btn" title="Play audio" />
      </Link>
    );
  }
  toggleView() {
    this.setState({
      showGridView: !this.state.showGridView
    });
  }
  onPlayAudio(media) {
    this.setState({
      currentAudio: media,
      showAudioView: !this.state.showAudioView
    });
  }
  render() {
    let audioCards = this.props.audioFiles.map((media, idx) => {
      return (
        <Col xs="12" md="4" key={idx}>
          <AudioCards
            link={this}
            playLink={this}
            onPlay={() => this.onPlayAudio(media)}
            onDelete={() => this.confirmDelete(media)}
            audioName={media.FileName}
            autoPlay={false}
            mute={true}
            id={idx}
            source={media.FilePath}
          />
        </Col>
      );
    });

    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Audios"
        buttonName="Add Audio"
        buttonLink={`${this.props.match.url}/audioUpload`}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              clear={false}
              simpleValue
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActiveAudioGrid
                audioFiles={this.props.activeAudios}
                playAudio={this.playAudio.bind(this)}
                onDeleteAudio={this.onDeleteAudio.bind(this)}
              />
            ) : (
              <InactiveAudioGrid
                audioFiles={this.props.inActiveAudios}
                playAudio={this.playAudio.bind(this)}
                onDeleteAudio={this.onDeleteAudio.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ToastContainer autoClose={1000} />
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this audio ?"
              : "Are you sure you want to activate this audio ?"
          }
        />
        <AudioPlayerModal
          title="audio"
          toggleFunction={this.onPlayAudio.bind(this)}
          openFlag={this.state.showAudioView}
          audio={this.state.currentAudio}
          muted={false}
          autoPlay={true}
        />
      </CardLayout>
    );
  }
}
const mapStateToProps = state => {
  return {
    audioFiles: state.mediaReducer.activeAudios,
    audioError: state.mediaReducer.audioError,
    activeAudios: state.mediaReducer.activeAudios,
    inActiveAudios: state.mediaReducer.inActiveAudios
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getAudioFiles: () => dispatch(actions.getAudioFiles()),
    deleteAudioFile: (id, audio) => dispatch(actions.deleteAudioFile(id, audio))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(AudioContent);



