import React, { Component } from "react";
import { Col, Row, FormGroup } from "reactstrap";
import CardLayout from "../../components/Cards/CardLayout";
import Loader from "../../components/Loader/Loader";
import * as actions from "../../store/actions";
import { connect } from "react-redux";
import VideoCards from "../../components/Cards/VideoCards";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import ActiveVideoGrid from "./ActiveVideoGrid";
import InActiveVideoGrid from "./InActiveVideoGrid";
import VideoPlayer from "../../components/VideoPlayer/VideoPlayer";
import ConfirmModal from "../../components/Modal/ConfirmModal";
import _ from "lodash";
import { Link } from "react-router-dom";
import DropdownSelect from "../../components/InputElement/Dropdown";
import * as constants from "../../constants/StatusConstants";
class VideoContent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      showGridView: false,
      modalStatus: false,
      videoToDelete: "",
      tableStatus: true
    };
  }
  componentDidMount() {
    this.props.getVideoFiles();
    let compRef = this;
    setTimeout(() => {
      compRef.setState({ loading: false });
      if (compRef.props.videoError) {
        Toaster.Toaster("Something went wrong !", compRef.props.videoError);
      }
    }, 2000);
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  toggleView() {
    this.setState({
      showGridView: !this.state.showGridView
    });
  }
  onDeleteVideo(cell, row) {
    if (this.state.tableStatus) {
      return (
        <Link to={this} onClick={() => this.confirmDelete(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    } else {
      return (
        <Link to={this} onClick={() => this.confirmDelete(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
  }
  confirmDelete(video) {
    this.setState({
      videoToDelete: video
    });
    this.onModalToggle();
  }
  onModalToggle() {
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
    let videoToDelete = { ...this.state.videoToDelete };
    videoToDelete.FilePath = videoToDelete.ServerPath;
    let video = _.pick(videoToDelete, [
      "Id",
      "VideoName",
      "FilePath",
      "Active"
    ]);
    let displayMessage = "";
    if (this.state.tableStatus) {
      video.Active = false;
      displayMessage = "Video deactivated successfully";
    } else {
      video.Active = true;
      displayMessage = "Video activated successfully";
    }
    this.props.deleteVideoFile(video.Id, video);
    this.setState({ loading: true });
    setTimeout(() => {
      let message = "";
      this.props.videoError
        ? (message = this.props.videoError)
        : (message = displayMessage);
      this.setState({ loading: false });
      Toaster.Toaster(message, this.props.videoError);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  playVideo(cell, row) {
    return (
      <div style={{ height: 100, width: 100 }}>
        <VideoPlayer
          dimensions="video-grid-dimens"
          autoPlay={false}
          source={row.FilePath}
          mute={true}
        />
      </div>
    );
  }
  render() {
    let videoCards = this.props.videoFiles.map((media, idx) => {
      return (
        <Col xs="12" md="4" key={idx}>
          <VideoCards
            link={this}
            onDelete={() => this.confirmDelete(media)}
            videoName={media.VideoName}
            id={idx}
            source={media.FilePath}
          />
        </Col>
      );
    });
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Videos"
        buttonName="Add Video"
        buttonLink={`${this.props.match.url}/videoUpload`}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              clear={false}
              simpleValue
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActiveVideoGrid
                videoFiles={this.props.activeVideos}
                playVideo={this.playVideo.bind(this)}
                onDeleteVideo={this.onDeleteVideo.bind(this)}
              />
            ) : (
              <InActiveVideoGrid
                videoFiles={this.props.inActiveVideos}
                playVideo={this.playVideo.bind(this)}
                onDeleteVideo={this.onDeleteVideo.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ToastContainer autoClose={1000} />
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this video ?"
              : "Are you sure you want to activate this video ?"
          }
        />
      </CardLayout>
    );
  }
}
const mapStateToProps = state => {
  return {
    videoFiles: state.mediaReducer.activeVideos,
    videoError: state.mediaReducer.videoError,
    activeVideos: state.mediaReducer.activeVideos,
    inActiveVideos: state.mediaReducer.inActiveVideos
  };
};
const mapDispatchToProps = dispatch => {
  return {
    getVideoFiles: () => dispatch(actions.getVideoFiles()),
    deleteVideoFile: (id, video) => dispatch(actions.deleteVideoFile(id, video))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(VideoContent);



